

a.wp-block-bergallblocks-marquee{
    display: block;
    text-decoration: none;
}


.marquee-wrapper {
    overflow: clip;
    width: 100%;
}

.marquee-wrapper.fadeout-horizontal{
    mask-image:  linear-gradient(
        to right,
        transparent,
        black 5rem,
        black calc(100% - 5rem),
        transparent
    );
    
}
.marquee-content,
.marquee-content .block-editor-block-list__layout{
    display: flex;
    align-items:baseline;
    line-height: 1rem;
    padding-left: 1rem;
    gap: 1rem;
}

.marquee-content{
    width: max-content;
    animation: marquee-scroll var(--speed, 10s) linear infinite var(--direction,
    forwards);
}



/* Animation */
@keyframes marquee-scroll {
    from{
        transform:  translateX(0%);
    }
    to {
        transform: translateX(-50%);
    }
}